<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级简介</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/editIntro'})">编辑</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox">
                <img src="../../assets/message/onShow.png" alt="">
            </div>
            <div class="content b_line">
                <p class="subtitle">班级简介</p>
                <p class="c666">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar tempor. </p>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img src="../../assets/message/onShow.png" alt="">
                    <img src="../../assets/message/onShow.png" alt="">
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <img src="../../assets/message/onShow.png" alt="">
                </div>
            </div>  
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            data:{}
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        getClassInfo(){//获取班级信息
            this.$http({url:this.$api.class.classIntro},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                // if(data.code == 0){
                //     this.list = data.data;
                // }else{
                //     this.$toast(data.msg);
                // }
            })
        }
    },
    created() {
        alert(1)
        this.getClassInfo();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem;
    }
    .nameBox img{
        width:100%;
        box-shadow: 0 0 0.3rem #ccc;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
    }
</style>




